<template>
  <div id="app">
    <p>
      Welcome to your Vue.js app with dropzone!
    </p>
    <dropzone ref="mydrop" id="myVueDropzone" url="https://httpbin.org/post" v-on:vdropzone-success="success"></dropzone>
    <button @click="removeFiles()">remove</button>
  </div>
</template>

<script>
  import Dropzone from '../src/index'
  export default {
    name: 'MainApp',
    components: {
      Dropzone
    },
    mounted(){ //newly added
      //setOption(optionname, value)
      this.$refs.mydrop.setOption('autoProcessQueue', false);
    },
    data () {
      return {
        processQueue: false,
        fa: true
      }
    },
    methods: {
      'success': function (file) {
        console.log('A file was successfully uploaded')
      },
      'process': function () {
        var newArr = this.$children.filter(function(item){
          return item.id === "myVueDropzone";
        });
        var component = newArr[0];
        component.processQueue()
      },
      removeFiles(){ //newly added
        let files = this.$refs.mydrop.getAcceptedFiles();  
        console.log(files);
      }
    }
  }
</script>

<style scoped>
  @import url('~dropzone/dist/dropzone.css');
  @import 'https://fonts.googleapis.com/css?family=Roboto';
</style>


